import { CalendarListingWrapper, CalendarListingTitle, CalendarListingSearchWrapper, CalendarListingContiner } from "../yourCalendarStyling";
import ListingItem from "./ListingItem/ListingItem";
import { YourCalendarListing } from "../yourCalendar";

interface ListingComponentProps {
    listings: YourCalendarListing[];
}

const ListingComponent: React.FC<ListingComponentProps> = ({ listings }) => {
    return (
        <CalendarListingWrapper>
            <CalendarListingTitle>Calendar</CalendarListingTitle>
            <CalendarListingSearchWrapper></CalendarListingSearchWrapper>
            <CalendarListingContiner>
                {listings.map((listing) => {
                    return <ListingItem listing={listing} key={listing.id} />;
                })}
            </CalendarListingContiner>
        </CalendarListingWrapper>
    );
};

export default ListingComponent;
